import { useFusionContext } from "../utils";
import { ActionType } from "../types";
export var useDialog = function () {
    var dispatch = useFusionContext().dispatch;
    return function (content, actions, config) {
        var id = "dialog-" + new Date().getTime().toString();
        var payload = { id: id, content: content, actions: actions, config: config };
        dispatch({ type: ActionType.ADD_DIALOG, payload: payload });
    };
};
