import { useFusionContext } from "../utils";
import { ActionType } from "../types";
export var useModal = function () {
    var dispatch = useFusionContext().dispatch;
    return function (Component, config) {
        var id = "modal-" + new Date().getTime().toString();
        var payload = { id: id, Component: Component, config: config };
        dispatch({ type: ActionType.ADD_MODAL, payload: payload });
    };
};
